.App {
  background-color: #5d5b65;
  color: #fff;
  height: 100%;
}

.card{
  border-radius: 0.5rem;
  margin: auto;
  width: 25%;
  height: 100%;
  background: #1A1B21;
}

.header {
  text-align: center;
}

.header img{
  width: 100%;
  border-radius: 0.5rem 0.5rem 0 0;
}

.header .bio, .header h1{
  margin: 0;
}

.header .bio {
  color: #F3BF99;
}

.website {
  font-size: 0.8rem;
}

.btn{
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  width: 100%;
}

button{
  font-family: monospace;
  padding: 0.8rem 1rem;
  border-radius: 0.5rem;
  border: none;
  width: 40%;
}

.email {
  color: black;
  vertical-align: middle;
}

.ln {
  vertical-align: middle;
  background-color: #5093E2;
  color: white;
}

.main {
  padding: 0 2rem;
}

h2 {
  margin-top: 0.5rem;
  margin-bottom: 0;
}

.small {
  font-size: 0.7rem;
  color: #DCDCDC;
}

span {
  padding: 0 1rem;
}

.footer{
  background-color: #161619;
  text-align: center;
  padding: 1rem 2rem;
  border-radius: 0 0 0.5rem 0.5rem;
}